<template>
  <div class="pelationshipBasicInformation">
    <div class="header">
      <el-select class="select" size="small" v-model="queryForm.gddwbq" placeholder="请选择供电单位标签" filterable clearable>
        <el-option
          v-for="item in gddwbqOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-select class="select" size="small" v-model="queryForm.yxhdbq" placeholder="请选择营销活动标签" filterable clearable>
        <el-option
          v-for="item in yxhdbqOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-button size="small" type="primary" @click="getTableData">查询</el-button>
      <el-button size="small" type="primary" @click="clickAdd">新增关联关系</el-button>
    </div>
    <div class="content">
      <!-- <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span class="text">客户关注热点趋势分析</span>
          </div>
      </div>-->
      <el-table
        class="content-table"
        border
        :data="tableData"
        :height="500"
        :row-class-name="tableRowClassName"
      >
        <el-table-column label="序号" align="center" type="index" width="70"></el-table-column>
        <el-table-column prop="ygid" label="关系编号"></el-table-column>
        <el-table-column prop="gddwbq" label="供电单位标签"></el-table-column>
        <el-table-column prop="yxhdbq" label="营销活动标签"></el-table-column>
        <el-table-column prop="zdtjjgs" label="最大推荐结果数"></el-table-column>
        <el-table-column prop="tjjgwkj" label="推荐结果为空时">
          <template slot-scope="scope">{{scope.row.tjjgwkj==='1'?'随机分配':'不处理'}}</template>
        </el-table-column>
        <el-table-column prop="cjsj" label="创建时间"></el-table-column>
        <el-table-column prop="gxsj" label="更新时间"></el-table-column>
        <el-table-column align="center" width="200" label="操作">
          <template slot-scope="scope">
            <span
              class="text"
              type="text"
              style="margin-right: 10px;"
              @click="clickEdit(scope.row)"
            >编辑任务</span>
            <span class="text" type="text" @click="clickCheck(scope.row)">查看推荐生成结果</span>
          </template>
        </el-table-column>
      </el-table>
      <div class="content-page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="this.queryForm.current"
          :page-sizes="[10,20,30,40]"
          :page-size="this.queryForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="this.queryForm.total"
        ></el-pagination>
      </div>
    </div>
    <el-dialog
      class="add-dialog"
      title="新增关联关系"
      :visible.sync="addDialogVisible"
      width="80%"
      :before-close="addDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ addDialogTitle }}</span>
      </span>
      <el-form
        class="addDialog-form"
        :model="addDialogForm"
        ref="addDialogForm"
        label-width="130px"
      >
        <div class="form-item">
          <div class="form-title">{{"关联标签设置:"}}</div>
          <div class="form-content">
            <el-table class="content-table" border :data="addDialogForm.labelList">
              <el-table-column prop="tagTargetId" label="供电单位的标签">
                <template slot-scope="scope">{{scope.row?'':scope.row}}
                  <el-select
                    size="mini"
                    style="width: 80%; padding: 10px"
                    filterable clearable
                    v-model="addDialogForm.tagSource"
                    placeholder="请选择"
                  >
                    <el-option
                      v-for="item in gddwbqOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </template>
              </el-table-column>
              <el-table-column prop="tagRecId" label="营销活动的标签">
                <template slot-scope="scope">{{scope.row?'':scope.row}}
                  <el-select
                    size="mini"
                    style="width: 80%; padding: 10px"
                    filterable clearable
                    v-model="addDialogForm.tagTarget"
                    placeholder="请选择"
                  >
                    <el-option
                      v-for="item in yxhdbqOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                  <!-- <span v-else>{{ scope.row.tagRecName }}</span> -->
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="form-item">
          <div class="form-title">{{"推荐结果设置:"}}</div>
          <div class="form-content" style=" border: 1px solid #ebeef5;">
            <el-form-item label="最大推荐结果数:">
              <el-input-number
                :min="1"
                :max="1000000"
                size="mini"
                class="input"
                style="width:200px"
                v-model="addDialogForm.recResultNumber"
              ></el-input-number>
            </el-form-item>
            <el-form-item label="推荐结果为空时:">
              <el-radio-group v-model="addDialogForm.recEmpty">
                <el-radio :label="'0'">不处理</el-radio>
                <el-radio :label="'1'">随机分配</el-radio>
              </el-radio-group>
              <span class="tip">{{"（当目标对象无具体推荐对象时，给目标对象随机分配主体内的对象）"}}</span>
            </el-form-item>
          </div>
        </div>
      </el-form>
      <div class="dialog-pagination"></div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="addDialogConfirm">确定</el-button>
        <el-button size="small" @click="addDialogClose">关闭</el-button>
      </span>
    </el-dialog>
    <el-dialog
      class="check-dialog"
      title="推荐生成结果"
      :visible.sync="checkDialogVisible"
      width="80%"
      :before-close="checkDialogClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ '推荐生成结果' }}</span>
      </span>
      <div class="query-group">
        <div class="search">
          <el-input
            class="input"
            size="small"
            v-model.trim="checkDialogForm.gddwmc"
            placeholder="请输入供电单位名称"
          ></el-input>
          <el-input
            class="input"
            size="small"
            v-model.trim="checkDialogForm.yxhdmc"
            placeholder="请输入营销活动名称"
          ></el-input>
          <el-select
            class="select"
            size="small"
            v-model="checkDialogForm.gddwbq"
            placeholder="请选择供电单位标签"
          >
            <el-option
              v-for="item in gddwbqOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-select
            class="select"
            size="small"
            v-model="checkDialogForm.yxhdbq"
            placeholder="请选择营销活动标签"
          >
            <el-option
              v-for="item in yxhdbqOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button size="small" type="primary" @click="getCheckDialogData">查询</el-button>
        </div>
      </div>
      <el-table
        class="dialog-table"
        border
        :data="checkDialogData"
        :height="400"
        :row-class-name="tableRowClassName"
      >
        <!-- <el-table-column prop="gddwbm" label="供电单位编码"></el-table-column> -->
        <el-table-column prop="gddwmc" label="供电单位"></el-table-column>
        <!-- <el-table-column prop="yxhdbh" label="营销活动编号"></el-table-column> -->
        <el-table-column prop="yxhdmc" label="营销活动名称"></el-table-column>
        <el-table-column prop="yxhdbq" label="营销活动编号"></el-table-column>
        <!-- <el-table-column prop="px" label="排序"></el-table-column> -->
      </el-table>
      <div class="dialog-pagination">
        <el-pagination
          @size-change="checkDialogSizeChange"
          @current-change="checkDialogCurrentChange"
          :current-page="this.checkDialogForm.current"
          :page-sizes="[10,20,30,40]"
          :page-size="this.checkDialogForm.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="this.checkDialogForm.total"
        ></el-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="checkDialogClose">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Interface1 from '@/base/mixin/precision-marketing';
import Interface2 from '@/base/mixin/common-api';
import {GDDW_TAG_OBJ,YXHD_TAG_OBJ} from '@/config'

export default {
  name: "pelationshipBasicInformation",
  components: {
  },
  mixins: [Interface1, Interface2],

  props: {},
  data() {
    return {
      queryForm: {
        gddwbq: "",
        yxhdbq: "",
        current: 1,
        size: 10,
        total: 0
      },
      gddwbqOptions: [],
      yxhdbqOptions: [],
      // 表格
      listId: '',
      tableData: [],
      // 新增框
      addDialogVisible: false,
      addDialogTitle: "新增关联关系",
      addDialogData: [],
      addDialogForm: {
        tagSource: "",
        tagTarget: "",
        labelList: [{}],
        recResultNumber: "",
        recEmpty: "",
        current: 1,
        size: 10,
        total: 0
      },
      tagSourceOptions: [],
      tagTargetOptions: [],
      // 查看框
      checkDialogVisible: false,
      checkDialogData: [],
      checkDialogForm: {
        gddwmc: "",
        yxhdmc: "",
        gddwbq: "",
        yxhdbq: "",
        current: 1,
        size: 10,
        total: 0
      },
      ygid:''
    };
  },
  mounted() {
    this.getGddwOptions()
    this.getYxhdOptions()
    this.getTableData()
  },
  methods: {
    //表格样式
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'success-row';
      }
      return '';
    },
    // 分页
    handleSizeChange(val) {
      this.queryForm.size = val
      this.getTableData()
    },
    handleCurrentChange(val) {
      this.queryForm.current = val
      this.getTableData()
    },
    // 点击新增
    clickAdd() {
      this.addDialogForm.tagSource= ''
      this.addDialogForm.tagTarget= ''
      this.addDialogForm.recResultNumber= ''
      this.addDialogForm.recEmpty= ''
      this.ygid = ''
      this.addDialogTitle = "新增关联关系"
      this.addDialogVisible = true
    },
    // 新增框关闭
    addDialogClose() {
      this.addDialogVisible = false
    },
    // 新增框确定
    addDialogConfirm() {
      if (!this.addDialogForm.tagSource){
        this.$message.warning('请选择供电单位标签')
        return
      }else if (!this.addDialogForm.tagTarget){
        this.$message.warning('请选择营销活动标签')
        return
      }
      if ( this.addDialogForm.recEmpty===''){
        this.$message.warning('请选择推荐结果为空时选项')
        return
      }
      let params = {
        "gddwbq":this.addDialogForm.tagSource,
        "yxhdbq":this.addDialogForm.tagTarget,
        "zdtjjgs":this.addDialogForm.recResultNumber,
        "tjjgwkj":this.addDialogForm.recEmpty
      }
      if (this.addDialogTitle==='编辑关联关系'){ //编辑
        params.ygid = this.ygid
        this.updateGddwStrategy(params).then(()=>{
          this.addDialogVisible = false
          this.getTableData()
        })
      }else {  //新增
        this.addGddwStrategy(params).then(()=>{
          this.addDialogVisible = false
          this.getTableData()
        })
      }
    },
    //编辑任务
    clickEdit(row) {
      this.addDialogForm.tagSource= row.gddwbqId
      this.addDialogForm.tagTarget= row.yxhdbqId
      this.addDialogForm.recResultNumber= row.zdtjjgs
      this.addDialogForm.recEmpty= row.tjjgwkj
      this.ygid = row.ygid
      this.addDialogTitle = "编辑关联关系"
      this.addDialogVisible = true
    },
    // 查看推荐生成结果
    clickCheck(row) {
      this.checkDialogForm.gddwbq= ''
      this.checkDialogForm.yxhdbq= ''
      this.checkDialogForm.gddwmc= ''
      this.checkDialogForm.yxhdmc= ''
      this.listId = row.ygid
      this.getCheckDialogData()
      this.checkDialogVisible = true
    },
    // 查看框关闭
    checkDialogClose() {
      this.checkDialogVisible = false
    },
    // 查看框分页
    checkDialogSizeChange(val) {
      this.checkDialogForm.size = val
      this.getCheckDialogData()
    },
    checkDialogCurrentChange(val) {
      this.checkDialogForm.current = val
      this.getCheckDialogData()
    },
    // 获取列表
    getTableData() {
      this.getGddwStrategy(this.queryForm).then(res => {
        this.tableData = [...res.records]
        this.queryForm.total = res.total
      })
    },
    // 获取新增列表
    getCheckDialogData() {
      this.getGddwResult({
        ygid: this.listId,
        "gddwbq":this.checkDialogForm.gddwbq,
        "yxhdbq":this.checkDialogForm.yxhdbq,
        "gddwmc":this.checkDialogForm.gddwmc,
        "yxhdmc":this.checkDialogForm.yxhdmc,
        size:this.checkDialogForm.size,
        current:this.checkDialogForm.current,
      }).then(res => {
        this.checkDialogData = [...res.records]
        this.checkDialogForm.total = res.total
      })
    },
    //获取供电单位标签
    getGddwOptions() {
      this.getLabel({
        tagobjId: GDDW_TAG_OBJ,
        gddwbq: "",
      }).then(res => {
        this.gddwbqOptions = res.map((v)=>{
          return {
            value:v.bqId,
            label:v.bqName,
          }
        })
      })
    },
    //获取营销用户标签
    getYxhdOptions() {
      this.getLabel({
        tagobjId: YXHD_TAG_OBJ,
        yxhdbq: "",
      }).then(res => {
        this.yxhdbqOptions = res.map((v)=>{
          return {
            value:v.bqId,
            label:v.bqName,
          }
        })
      })
    },
  }
};
</script>

<style scoped lang="scss">
.pelationshipBasicInformation {
  ::v-deep .el-table .warning-row {
    // background: #e7f6f7;
    background: #e9f3fe;
  }
  ::v-deep .el-table {
    .success-row {
      background: rgb(244, 244, 244);
    }
    .cell {
      padding: 2px 4px;
    }
    th {
      background-color: rgb(24, 153, 250);
      .cell {
        color: #ffffff;
      }
    }
  }
  .content-border {
    background: #ffffff;
    border: 2px solid rgba(26, 132, 244, 0.2);
    border-radius: 4px;
  }
  .header {
    height: 40px;
    display: flex;
    align-items: center;
    .select {
      margin-right: 20px;
    }
    .input {
      width: 300px;
      margin-right: 20px;
    }
  }
  .content {
    .content-table {
      width: 100%;
      // height: 350px;
      .text {
        color: #1899fa;
        cursor: pointer;
      }
    }
    .content-page {
      margin-top: 10px;
      width: 100%;
      text-align: center;
    }
  }
  .check-dialog,
  .add-dialog {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    ::v-deep .el-radio.is-checked .el-radio__inner {
      background: #409eff !important;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .query-group {
      margin-bottom: 10px;
      .search {
        display: flex;
        align-items: center;
      }
      .select {
        margin-right: 20px;
      }
      .input {
        width: 300px;
        margin-right: 20px;
      }
    }
    .dialog-pagination {
      text-align: center;
      margin: 10px 0;
    }
  }
  .add-dialog {
    .addDialog-form {
      .el-form-item {
        margin-bottom: 10px;
      }
      .form-item {
        width: 100%;
        display: flex;
        align-items: center;
        margin-bottom: 10px;
        .form-title {
          width: 160px;
          text-align: right;
          padding-right: 20px;
        }
        .form-content {
          width: calc(100% - 160px);
          .tip {
            display: block;
          }
        }
      }
    }
  }
}
</style>
