<script>
import pelationshipBasicInformation from "./components/pelationshipBasicInformation";
import lelationalPolicyLibrary from "./components/lelationalPolicyLibrary";
import Interface from '@/base/mixin/power-sensitive-users';
export default {
  name: "marketingService",
  components: {
    pelationshipBasicInformation,
    lelationalPolicyLibrary
  },
  mixins: [Interface],
  props: {},
  data() {
    return {
      //展示列表
      changeTypeActive: "1",
    };
  },
  mounted() {
  },
  methods: {
    //切换列表展示
    changeType(val) {
      this.changeTypeActive = val;
    },
  }
};
</script>

