    border-radius: 4px;
  }

  .content {
    // height: 500px;
    margin-bottom: 20px;
    .header {
      padding: 0 10px;
      box-sizing: border-box;
    }
    .main {
      height: calc(100% - 68px);
      padding: 10px;
      box-sizing: border-box;
    }
  }

  .change-type {
    width: 100%;
    height: 30px;
    display: flex;
    align-self: start;
    margin: 10px 0 10px;
    .active {
      &.change-button {
        color: #fff;
      }
      background-color: #1899fa;
    }
    .change-button {
